import styled from "styled-components";

import {
	Close,
	Content, Portal } from "@radix-ui/react-popover";

import {
	colors, marginsCss, spacingMap,
} from "../styleguide";
import { pxToRem } from "../utils/pxToRem";

import { StyledPopoverProps } from "./Popover.types";

export const StyledPopoverPortal = styled(Portal)<StyledPopoverProps>`
	position: absolute;
	background:  ${colors.white};
	width: ${({ $menuWidth }) => $menuWidth || pxToRem(300)};
	transition: opacity .1s cubic-bezier(.215, .61, .355, 1);
`;

export const StyledPopoverContent = styled(Content)<StyledPopoverProps>`
	overflow-x: hidden;
	display: flex;
	margin: initial;
	flex-direction: column;
	justify-content: center;
	background: ${colors.white};
	${marginsCss}

	align-items: flex-start;
	position: relative;
	min-width: fit-content;
	height:  ${({ $menuHeight }) => $menuHeight || "fit-content"};

	overflow-y: scroll;
	::-webkit-scrollbar{
		display: none;
	}
	width: ${({ $menuWidth }) => $menuWidth || "fit-content"};
	box-shadow: 0 ${spacingMap.lg} ${spacingMap.lg} -8px ${colors.black}24,
	0 ${spacingMap.sm} ${spacingMap.sm} ${colors.black}24, 0 ${spacingMap.xs} ${spacingMap.xs} ${colors.black}24,
	0 ${spacingMap.xxs} ${spacingMap.xxs} ${colors.black}24, 0 2px 2px ${colors.black}24;
	border-radius: 8px;

	// default will be auto which is set by Radix
	z-index: ${({ $zIndex }) => $zIndex && $zIndex};
`;


export const StyledPopoverCloseButton = styled(Close)`
	background: transparent;
	border: none;
	display: flex;
	align-items: center;
	justify-content: center;

	&:focus-within, &:active, &:focus-visible, &:hover {
		background: transparent;
		border: none;
		outline: none;
	}
`;
